.unknown{
    color:rgb(151, 110, 39);
}
.signOff {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 50%;

    transform: translate(-50%, -50%);
    letter-spacing: 2;
    left: 50%;
    top: 10%;
    font-family: "Clip";
    text-transform: uppercase;
    font-size: 6em;
    color: #{$color3};

}
.sign {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 50%;
    background-image: radial-gradient(ellipse 50% 35% at 50% 50%,
            #{$color3},
            transparent);
    transform: translate(-50%, -50%);
    letter-spacing: 2;
    left: 50%;
    top: 10%;
    font-family: "Clip";
    text-transform: uppercase;
    font-size: 6em;
    color: #{$color4};
    text-shadow: 0 0 0.6rem #{$color4}, 0 0 1.5rem #{$color1},
        -0.2rem 0.1rem 1rem #{$color1}, 0.2rem 0.1rem 1rem #{$color1},
        0 -0.5rem 2rem #{$color2}, 0 0.5rem 3rem #{$color2};
    animation: shine 2s forwards, flicker 3s infinite;
}

@keyframes blink {

    0%,
    22%,
    36%,
    75% {
        color: #{$color4};
        text-shadow: 0 0 0.6rem #{$color4}, 0 0 1.5rem #{$color1},
            -0.2rem 0.1rem 1rem #{$color1}, 0.2rem 0.1rem 1rem #{$color1},
            0 -0.5rem 2rem #{$color2}, 0 0.5rem 3rem #{$color2};
    }

    28%,
    33% {
        color: #{$color1};
        text-shadow: none;
    }

    82%,
    97% {
        color: #{$color2};
        text-shadow: none;
    }
}

.flicker {
    animation: shine 2s forwards, blink 3s 2s infinite;
}

.fast-flicker {
    animation: shine 2s forwards, blink 10s 1s infinite;
}

@keyframes shine {
    0% {
        color: #{$color3};
        text-shadow: none;
    }

    100% {
        color: #{$color4};
        text-shadow: 0 0 0.6rem #{$color4}, 0 0 1.5rem #{$color1},
            -0.2rem 0.1rem 1rem #{$color1}, 0.2rem 0.1rem 1rem #{$color1},
            0 -0.5rem 2rem #{$color2}, 0 0.5rem 3rem #{$color2};
    }
}

