.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.fighterFont {
  font-family: "FightingSpirit2", sans-serif;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

$url : "./img/banner-min.jpg";
$url2 : "./img/banneraltered-min.png";
$url4 : "./img/banneralteredwithouttext-min.png";
$url3 : "./img/bannerwaiting-min.jpg";
$line-width: 5px;

.glitchImageAlteredWaiting {
  background: url(#{$url3});
}

.glitchImageAltered {
  background: url(#{$url2});
}

.glitchImageAlteredWithoutText {
  background: url(#{$url4});
}

.glitchImage {
  background: url(#{$url});
}

.glitchImage,
.glitchImageAltered,
.glitchImageAlteredWithoutText,
.glitchImageAlteredWaiting {
  position: relative;
  // transform: translate(-10px, 0);
  width: 90%;
  height: 90%;
  background-size: cover;
}

.glitchImage:before {
  background: url(#{$url}) radial-gradient(#0F0,
    #000),
  repeating-linear-gradient(transparent 0, rgba(0, 0, 0, 1) $line-width/2, transparent $line-width );
}

.glitchImageAltered:before {
  background: url(#{$url2}) radial-gradient(#0F0,
    #000),
  repeating-linear-gradient(transparent 0, rgba(0, 0, 0, 1) $line-width/2, transparent $line-width );
}

.glitchImageAlteredWaiting:before {
  background: url(#{$url3}) radial-gradient(#0F0,
    #000),
  repeating-linear-gradient(transparent 0, rgba(0, 0, 0, 1) $line-width/2, transparent $line-width );
}


.glitchImageAlteredWithoutText:before {
  background: url(#{$url4}) radial-gradient(#0F0,
    #000),
  repeating-linear-gradient(transparent 0, rgba(0, 0, 0, 1) $line-width/2, transparent $line-width );
}

.glitchImage:before,
.glitchImageAltered::before,
.glitchImageAlteredWithoutText::before,
.glitchImageAlteredWaiting::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: contain no-repeat;
  /* contain for split effect */
  opacity: 0.5;
  mix-blend-mode: hard-light;
  animation: glitchImage2 10s linear infinite;

  background-size: contain no-repeat;
  background-position: center;
  background-blend-mode: overlay;

  [class$=-effect] img {
    vertical-align: top !important;
    margin: 0 !important;
    opacity: 0 !important;
  }

  // border: 1px solid black;
}

.glitchImage:hover:before,
.glitchImageAltered:hover:before,
.glitchImageAlteredWithoutText:hover:before,
.glitchImageAlteredWaiting:hover:before {
  animation: glitchImage1 1s linear infinite;
}

.vignette {
  -webkit-box-shadow:
    inset 0 0 60px #000,
    inset 0 0 60px #000,
    inset 0 0 60px #000;

  -moz-box-shadow:
    inset 0 0 60px #000,
    inset 0 0 60px #000,
    inset 0 0 60px #000;

  box-shadow:
    inset 0 0 60px #000,
    inset 0 0 60px #000,
    inset 0 0 60px #000;
}

@keyframes glitchImage1 {
  0% {
    background-position: 0 0;
    filter: hue-rotate(0deg);
  }

  10% {
    background-position: 5px 0;
  }

  20% {
    background-position: -5px 0;
  }

  30% {
    background-position: 15px 0;
  }

  40% {
    background-position: -5px 0;
  }

  50% {
    background-position: -25px 0;
  }

  60% {
    background-position: -50px 0;
  }

  70% {
    background-position: 0 -20px;
  }

  80% {
    background-position: -60px -20px;
  }

  81% {
    background-position: 0 0;
  }

  100% {
    background-position: 0 0;
    filter: hue-rotate(360deg);
  }
}

@keyframes glitchImage2 {
  0% {
    background-position: 0 0;
    filter: hue-rotate(0deg);
  }

  10% {
    background-position: 15px 0;
  }

  15% {
    background-position: -15px 0;
  }

  20% {
    filter: hue-rotate(360deg);
  }

  25% {
    background-position: 0 0;
    filter: hue-rotate(0deg);
  }

  100% {
    background-position: 0 0;
    filter: hue-rotate(0deg);
  }
}